@import "../styles/variables.scss";

.project {
    display: flex;
    min-height: 380px;
    box-shadow: rgba(0, 0, 0, 0.1) 0px 4px 6px -1px, rgba(0, 0, 0, 0.06) 0px 2px 4px -1px;
    margin-bottom: 48px;
    .project-image {
        flex: 1;
        padding: 48px;
        background-color: $bgColor;
        img{
            width: 100%;
            height: 100%;
            border-radius: $raduis
        }
    }
    .project-info{
         flex: 1;
         padding: 48px;
         h2 {
            margin-bottom: 24px;
            color: $titleColor;
            font-weight: $semiBold;
         }
         p {
            color: $textColor;
            margin-bottom: 24px;
        }
        .tools {
            display: flex;
            gap: 8px;
            margin-bottom: 24px;
            flex-wrap: wrap;
        
        }
    }
}